<template>
    <div id="view-employee">
        <h3 class="center">View Employee</h3>
        <table class="collection with-header">
            <tr>
                <th class="collection-header">Name</th>
                <td>{{name}}</td>
            </tr>
            <tr>
                <th class="collection-header">DOB</th>
                <td>{{dob}}</td>
            </tr>
            <tr>
                <th class="collection-header">ID</th>
                <td>{{employee_id}}</td>
            </tr>
            <tr>
                <th class="collection-header">Department</th>
                <td>{{department}}</td>
            </tr>
            <tr>
                <th class="collection-header">Access</th>
                <td>Is Employee: {{is_employee}}  Is Agent: {{is_agent}}  is_admin: {{is_admin}}</td>
            </tr>
        </table>
        <br />
        <div>
            <router-link to="/Employee" class="btn gray left">Back</router-link>
            
            <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn blue right"><i class="fa fa-pencil"></i></router-link>
        
        </div>
    </div>
</template>

<script>

import db from './firebaseInit.js';

export default {
    name: 'view-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            department: null,
            dob: null,
            is_admin: null,
            is_agent: null,
            is_employee: null,
            position: null,
        }   
    }, 
    beforeRouteEnter (to, from, next) {
        db.collection('employees').where('employee_id', 
        '==', to.params.employee_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.department = doc.data().department
                    vm.dob = doc.data().dob_mmddyyyy
                    vm.is_admin = doc.data().is_admin
                    vm.is_agent = doc.data().is_agent
                    vm.is_employee = doc.data().is_employee
                    vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                db.collection('employees').where('employee_id', 
                '==', 
                this.$route.params.employee_id).get()
                .then(querrySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.department = doc.data().department
                        this.dob_mmddyyyy = doc.data().dob_mmddyyyy
                        this.employee_id = doc.data().employee_id
                        this.is_admin = doc.data().is_admin
                        this.is_agent = doc.data().is_agent
                        this.is_employee = doc.data().is_employee
                        this.name = doc.data().name
                        this.position = doc.data().position
                    })
                })
            },
            ///Add aditional methods here
        }
    }
</script>
