<template>
    <div id="new-employee">
        <h3 class="center">New Employee</h3>
        <form @submit.prevent="saveEmployee" class="col s12">
            <div class="input-field col s12">
                <input type="text" v-model="name" required /> <label>Last Name, First Name</label>
            </div>
            <div class="input-field col s12">
                <input type="text" v-model="employee_id" required /><label> Employee ID</label>
            </div>
             <div class="input-field col s12">
                <input type="text" v-model="dob" required /><label>Date of Birth (MM/DD/YYYY)</label>
            </div>
             <div class="input-field col s12">
                <input type="text" v-model="department" required /><label>Department</label>
            </div>
             <div class="input-field col s12">
                <input type="text" v-model="position" required /><label>Position</label>
            </div>
            <div>
                <router-link to="/employee" class="btn grey left">Back</router-link>
                <button type="reset" class="btn red center">Reset</button>
                <button type="submit" class="btn green right">Submit</button>
            </div>
        </form>
    </div>
</template>

<script>
import db from './firebaseInit.js'

export default {
    name: 'new-employee',
    data () {
        return {
           department: null,
           dob_mmddyyyy: null,
           employee_id: null,
           is_admin: null,
           is_agent: null,
           is_employee: null,
           name: null,
           position: null
        }
    },
    methods: {
        saveEmployee () {
            db.collection('employees').add({
                department: this.department,
                dob_mmddyyyy: this.dob_mmddyyyy,
                employee_id: this.employee_id,
                is_admin: false,
                is_agent: false,
                is_employee: true,
                name: this.name,
                position: this.position
            }).then(docRef => this.$router.push('/employee')).catch(error => console.log(error))
        }
    }
}
</script>
