<template>
    <div id="employee-dashboard">
        <h3 class="center">Employee Dashboard</h3>
        <table class="collection with-header">
            <tr class="collection-header">
                <th>Employee ID</th>
                <th>Name</th>
                <th>Department</th>
                <th>Position</th>
                <th>Is Active Employee</th>
                <th>Is Agent</th>
                <th>Is Admin</th>
                <th>View</th>
            </tr>
            <tr v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
                <td> {{employee.employee_id}}</td>
                <td>{{employee.name}}</td>
                <td>{{employee.department}}</td>
                <td>{{employee.position}}</td>
                <td>{{employee.is_employee}}</td>
                <td>{{employee.is_agent}}</td>
                <td>{{employee.is_admin}}</td>
                <td><router-link v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}" class="secondary-content">
                        <i class="fa fa-eye"></i>
                    </router-link></td>
            </tr>
        </table>
        <div class="fixed-action-btn">
            <router-link to="/employee/new" class="btn-floating btn-large red"><i class="fa fa-plus"></i></router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'employee-dashboard',
    data () {
        return {
           employees: [] 
        }
    },
    created () {
        db.collection('employees').orderBy('employee_id').get().then(querrySnapshot => {
            querrySnapshot.forEach(doc => {
                console.log(doc);
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dob': doc.data().dob_mmddyyyy,
                    'department': doc.data().department,
                    'position': doc.data().position,
                    'is_employee': doc.data().is_employee,
                    'is_agent': doc.data().is_agent,
                    'is_admin': doc.data().is_admin
                }
                this.employees.push(data)
            })
        })
    }
}
</script>
