<template>
    <div id="edit-employee">
        <h3 class="center">Edit Employee</h3>
        <div class="row">
         <form @submit.prevent="updateEmployee" class="col s12">
            <div class="input-field col s12">
                <input type="text" v-model="name" required /><label class="active">Employee Name</label>
            </div>
            <div class="input-field col s12">
                <input type="text" v-model="employee_id" disabled /><label class="active">Employee ID</label>
            </div>
             <div class="input-field col s12">
                <input type="text" v-model="dob_mmddyyyy" required /><label class="active">Date of Birth</label>
            </div>
             <div class="input-field col s12">
                <input type="text" v-model="department" required /><label class="active">Department</label>
            </div>
             <div class="input-field col s12">
                <input type="text" v-model="position" required /><label class="active">Position</label>
            </div>
            <div>
                <router-link to="/employee" class="btn grey left">Back</router-link>
                <button type="submit" class="btn green right">Submit</button>
            </div>
        </form>
    </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-employee',
    data () {
        return {
            department: null,
            dob_mmddyyyy: null,
            employee_id: null,
            is_admin: null,
            is_agent: null,
            is_employee: null,
            name: null,
            position: null
        }
    },
     beforeRouteEnter (to, from, next) {
        db.collection('employees').where('employee_id', 
        '==', to.params.employee_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.department = doc.data().department
                    vm.dob_mmddyyyy = doc.data().dob_mmddyyyy
                    vm.is_admin = doc.data().is_admin
                    vm.is_agent = doc.data().is_agent
                    vm.is_employee = doc.data().is_employee
                    vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData () {
                db.collection('employees').where('employee_id', 
                '==', 
                this.$route.params.employee_id).get()
                .then(querrySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.department = doc.data().department
                        this.dob_mmddyyyy = doc.data().dob_mmddyyyy
                        this.employee_id = doc.data().employee_id
                        this.is_admin = doc.data().is_admin
                        this.is_agent = doc.data().is_agent
                        this.is_employee = doc.data().is_employee
                        this.name = doc.data().name
                        this.position = doc.data().position
                    })
                })
            },
            updateEmployee () {
                db.collection('employees').where('employee_id', 
                '==', 
                this.$route.params.employee_id).get()
                .then(querrySnapshot => {
                    querrySnapshot.forEach(doc => {
                        doc.ref.update({
                            department: this.department,
                            dob_mmddyyyy: this.dob_mmddyyyy,
                            employee_id: this.employee_id,
                            is_admin: this.is_admin,
                            is_agent: this.is_agent,
                            is_employee: this.is_employee,
                            name: this.name,
                            position: this.position
                        })
                        .then(() => {
                        this.$router.push({
                            name: 'view-employee', 
                            params: {
                                employee_id: this.employee_id
                            }
                        })
                    })
                })
            })
        }
    }
}
</script>
